.headerWrap {
  width: 100%;
  background: #fff;
}
.header {
  max-width: 1920px;
  height: 100px;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin: 0 auto;
}
.btn {
  padding: 20px 40px;
  background: #F15E75;
  border-radius: 16px;
  border: none;
  cursor: pointer;
  color: white;
  font-family: 'Red Hat Display', sans-serif;
  font-weight: 500;
}